import React from 'react';
import {NavLink} from 'react-router-dom';
function Nav(){
    return(
        <>
            <nav>
                <div className="navbar">
                    <NavLink to="/">Portfolio</NavLink>
                    <ul>
                        <li><NavLink className="nav" to="/">Pesonal Info</NavLink></li>
                        <li><NavLink className="nav" to="/About">My Self</NavLink></li>
                        <li><NavLink className="nav" to="/Project">Project</NavLink></li>
                        <li><NavLink className="nav" to="/Contact">Contact</NavLink></li>
                        <li><NavLink className="nav" to="/Practice">Practice</NavLink></li>
                    </ul>
                </div>
            </nav>
        </>
    );
}
export default Nav;