import React, { useState } from 'react'
import Back from '../Component/Back'

const IncDecNumber = () => {
    const [num, setNum]=useState(0); //Use State
    const incNum=()=>{
        setNum(num+1);
    }
    const decNum=()=>{

        // If Else statement
        // if(num > 0)
        // setNum(num-1);
        // else{
        //     setNum(0);
        //     alert("Sorry, Zero limit reached")
        // }

        num > 0 ? setNum(num-1) : setNum(0) || alert("Sorry, Zero Limit Reached"); //Ternary Operater
    }
    
    return (
        <>
        <Back/>
        <div className="center_div">
        <div className="IncDecNum">
       <h1>{num}</h1>
       <div>
       <button className="btn1" onClick={incNum}>Increment</button>
       <button className="btn2" onClick={decNum}>Decrement</button>
       </div>
        
      </div>
      </div>
    
        </>
    )
    
}

export default IncDecNumber;
