import { Redirect, Route ,Switch} from 'react-router-dom';
import DigitalClock from './Practices/DigitalClock';
import Input from './Practices/Input';
import IncDecNumber from './Practices/IncDecNumber';
import CountDown from './Practices/CountDown';

