 function App(){
    return(
    <>
        
        
        {/* <DateTime/>
        <Greetings/>
        <Gallery/>
         <h1 className="card">Cards</h1> */}
         {/* <div className="card1"> */}
        {/* {Sdata.map(ncard)} arry map method */}
        {/* </div> */}
        <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path="/About" component={About} />
            <Route exact path="/Project" component={Project} />
            <Route exact path="/Contact" component={Contact} />
            <Route exact path="/Practice" component={Practice} />
            <Route exact path="/date_time" component={DateTime} />
            <Route exact path="/greeting" component={Greetings} />
            <Route exact path="/digitalclock" component={DigitalClock} />
            <Route exact path="/gallery" component={Gallery} />
            <Route exact path="/input" component={Input} />
            <Route exact path="/incDecNumber" component={IncDecNumber} />
            <Route exact path="/countdown" component={CountDown} />
            <Redirect to="/"/>
        </Switch>
    </>
    );
}
export default App;