import React from 'react';
import Back from '../Component/Back';

const cssStyle = {};//css inline style
const date = new Date().getHours(); //get time for greetings
let greetings= '';
if(date >= 1 && date <12){
  greetings = " Good Morning";
  cssStyle.color='green';
}
else if(date >= 12 && date <17){
  greetings = " Good Afternoon";
  cssStyle.color= 'yellow';
}
else if(date >= 17 && date <19){
  greetings = " Good Evening";
  cssStyle.color = 'skyBlue';
}
else{
  greetings = " Good Night";
  cssStyle.color= 'black';
}

function Greetings(){
    return(
      <>
      <Back/>
        <div className="center_div">
        {/* clock wishes */}
        <h1 className="greetings">Hello I am Arshad, <span style={cssStyle}>{greetings}</span></h1>
        </div>
    </>
    );
}
export default Greetings;