import React from 'react';
import Back from '../Component/Back';


function Gallery( ){
    const data=[
        {
            id:1,
            img:"https://picsum.photos/210/300",
        },
        {
            id:2,
            img:"https://picsum.photos/220/300",
        },
        {
            id:3,
            img:"https://picsum.photos/230/300",
        },
        {
            id:4,
            img:"https://picsum.photos/240/300",
        },
        {
            id:5,
            img:"https://picsum.photos/250/300",
        },
    ];
    const arraydata = data.map((val, i)=>{
        return(
            <img src={data[i].img}/>
        );
        
    });

    return(
    <>{/* gallery */}
    <Back/>
        <h1 style={{textAlign:"center"}}>Gallery</h1>
        <div className="gallery center_div">
        <div>
        {arraydata}
        </div>
        
        </div>
    </>
    );
}
export default Gallery;


