import React from 'react'
import { NavLink } from 'react-router-dom'

const PracticeHeading = () => {
    return (
        <>
        
                <div className="heading_link"> 
                    <NavLink to="/date_time">Date And Time</NavLink> 
                    <NavLink to="/greeting">Greetings</NavLink> 
                    <NavLink to="/digitalclock">Digital Clock</NavLink> 
                    <NavLink to="/gallery">Gallery</NavLink> 
                    <NavLink to="/input">Input</NavLink> 
                    <NavLink to="/incDecNumber">IncDecNumber</NavLink> 
                    <NavLink to="/countdown">CountDownTimer</NavLink> 
                    

                </div>
           
        </>
    )
}

export default PracticeHeading;
