import React from 'react';
// import Images from 'first-app/'

function Card(props){
    return(
        <>
        <div >
      
        <div className="card">
            <a href={props.href} target={props.target}>
            <img src={props.imgSrc}/>
            </a>
            <h2> {props.title} </h2>
            <p>{props.para}</p>
        </div>
        </div>
        </>
    );

}
export default Card;