import React from 'react';
import Card from '../Component/Card';
// import DateTime from '../Practices/DateTime';
// import Gallery from '../Practices/Gallery';
// import Greetings from '../Practices/Greetings';
import Nav from '../Component/Nav';
import DigitalClock from '../Practices/DigitalClock';
import PracticeHeading from '../Component/PracticeHeading';

// arry map method in js
function ncard(val){
    return(
        <>    
            <Card 
                key={val.id}
                imgSrc={val.imgSrc}
                title={val.title} 
                para={val.para}
                href={val.href}
                target={val.target}/>
        
        </>
    );
}
const Practice = ()=>{
    return(
        <>
        <div className="practice">
            <Nav/>
            <div className="P_heading">
                <PracticeHeading/>
            </div>
            
            
        <br/>
       
        
        
        </div>
        </>
    );

} 
export default Practice;